.App {
  font-family: sans-serif;
  text-align: left;
}

.align-content {
  display: flex;
  justify-content: center;
}

h1 {
  margin-left: 25px;
}

.table-input th {
  background: #9158ca;
  color: #fff;
}

.table th {
  background: #9158ca;
  color: #fff;
}

.table-input td,
th {
  border: none;
  text-align: center;
}

.table td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  width: 100px;
}

.table td {
  border: 1px solid #9a9a9a;
}

.table tr:nth-child(even) {
  background-color: #eeeeee;
}

.form {
  border: 1px solid #cecece;
  max-width: 250px;
}

.group {
  padding: 7px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border: 1px solid #cecece;
}

.table-input input[type="number"] {
  border-radius: 4px;
  height: 25px;
  width: 90px;
  padding: 2px 10px;
  border: 1px solid #cecece;
}

.form {
  margin-bottom: 20px;
  margin-right: 20px;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  padding: 20px;
}

.table-input {
  margin: 20px 0;
}
